'use client';

import React, { useEffect, useMemo, useRef, useState } from 'react';
import { motion, useInView } from 'framer-motion';
import { useLanguage } from '@/lib/i18n/LanguageProvider';
import './Journey.css';

type Act = {
  id: string;
  title: string;
  range: string;
  lines: string[];
};

type TimelineItem = {
  year: string;
  title: string;
  description: string;
  tag: string;
  tone: string;
};

const Journey: React.FC = () => {
    // Timeline milestones (right column)
    const timelineItems: TimelineItem[] = [
      {
        year: '2012',
        title: 'First laptop & the studio spark',
        description: 'An old laptop in a village photo studio sparked everything.',
        tag: 'Curiosity',
        tone: '#6366f1',
      },
      {
        year: '2016',
        title: 'Viral frame & first freelance work',
        description: 'A simple Facebook frame blew up; I started getting freelance work locally and online.',
        tag: 'Turning point',
        tone: '#c084fc',
      },
      {
        year: '2018',
        title: 'First agency attempt',
        description: 'Started a small marketing agency — it failed, but it built my foundation.',
        tag: 'Failure',
        tone: '#f59e42',
      },
      {
        year: '2020',
        title: 'Restaurant collapse during Covid',
        description: 'Lost the business overnight; discipline and patience became survival tools.',
        tag: 'Risk',
        tone: '#f87171',
      },
      {
        year: '⭐ 2022',
        title: 'Dubai: tools, speed, and real growth',
        description: 'Dubai gave me access I never had before: high-speed internet, good tools, a real workspace, and a position where learning finally compounded. I learned fast — and I’m still learning.',
        tag: 'Turning point',
        tone: '#38bdf8',
      },
      {
        year: '2024 → Now',
        title: 'Hybrid creative + AI',
        description: 'A blend of creativity, marketing, systems, and AI workflows.',
        tag: 'Now',
        tone: '#34d399',
      },
    ];
  const sectionRef = useRef<HTMLDivElement | null>(null);
  const isInView = useInView(sectionRef, { once: false, margin: '-80px' });
  const { t, language } = useLanguage();
  const isRTL = language === 'ar';
  const [activeIndex, setActiveIndex] = useState(0);

  // New acts for the story spine (left column)
  const acts: Act[] = [
    {
      id: 'curiosity',
      title: 'Act I — Curiosity',
      range: '2012–2016',
      lines: [
        'An old Dubai laptop changed everything in a tiny village studio.',
        'I learned Photoshop by breaking things more than fixing them.',
        'In 2016, a simple Facebook frame I made went unexpectedly viral.',
        'Local clients and online marketplace jobs started coming in.',
        'It was the first time I felt my skills could open real doors.'
      ],
    },
    {
      id: 'responsibility',
      title: 'Act II — Responsibility',
      range: '2017–2021',
      lines: [
        'Dropped my diploma to support my family.',
        'Started a small agency — it failed, but it shaped my discipline.',
        'Tried running a restaurant — Covid shut it down and brought debt.',
        'Worked any job possible while learning at night.',
        'Those years built resilience no certificate can teach.'
      ],
    },
    {
      id: 'focus',
      title: 'Act III — Dubai & Focus',
      range: '2022–Now',
      lines: [
        'Sold the family cow to buy a one-way ticket to Dubai.',
        'Took a low-paid job but learned more in a year than ever before.',
        'Dubai gave me real tools, fast internet, and space to grow properly.',
        'Explored performance marketing, web/app, systems, and AI.',
        'Still learning, always learning — growth doesn’t stop.'
      ],
    },
  ];

  const takeaways = useMemo(
    () => [
      'I care about results, not certificates.',
      'Calm under pressure; allergic to toxic environments.',
      'You get creative + marketing + tech in one person.'
    ],
    []
  );

  const cardVariants = {
    hidden: { opacity: 0, y: 12, filter: 'blur(8px)' },
    visible: {
      opacity: 1,
      y: 0,
      filter: 'blur(0px)',
      transition: {
        duration: 0.6,
        ease: 'easeOut' as const,
      },
    },
  };

  const actVariants = {
    hidden: { opacity: 0, y: 14 },
    visible: (index: number) => ({
      opacity: 1,
      y: 0,
      transition: { delay: 0.1 * index, duration: 0.5, ease: 'easeOut' as const },
    }),
  };

  const TimelineCard: React.FC<{ item: TimelineItem; index: number }> = ({ item, index }) => {
    const cardRef = useRef<HTMLDivElement | null>(null);
    const cardInView = useInView(cardRef, { once: false, margin: '-40px' });

    useEffect(() => {
      if (cardInView) setActiveIndex(index);
    }, [cardInView, index]);

    return (
      <motion.article
        ref={cardRef}
        className={`timeline-card ${cardInView ? 'active' : ''}`}
        variants={cardVariants}
        initial="hidden"
        animate={isInView ? 'visible' : 'hidden'}
        transition={{ delay: index * 0.05 }}
      >
        <div className="timeline-dot" style={{ borderColor: item.tone, boxShadow: `0 0 0 4px ${item.tone}1a` }} />
        <div className="timeline-body">
          <div className="timeline-meta">
            <span className="timeline-year">{item.year}</span>
            <span className="timeline-tag" style={{ color: item.tone, backgroundColor: `${item.tone}1a` }}>
              {item.tag}
            </span>
          </div>
          <h3 className="timeline-title">{item.title}</h3>
          <p className="timeline-description">{item.description}</p>
        </div>
      </motion.article>
    );
  };

  const glowPercent =
    timelineItems.length > 1 ? (activeIndex / Math.max(timelineItems.length - 1, 1)) * 100 : 0;

  return (
    <section
      id="journey"
      className={`fold journey-section ${isRTL ? 'rtl' : ''}`}
      ref={sectionRef}
    >
      <div className="container">
        <motion.div
          className="journey-framing"
          initial={{ opacity: 0, y: 22 }}
          animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 22 }}
          transition={{ duration: 0.6 }}
        >
          <span className="section-label">My Journey</span>
          <h2>Not a Corporate Story</h2>
          <p className="journey-kicker">
            I’m not telling this story in a corporate tone because my journey didn’t grow in corporate conditions. It didn’t start with perfect opportunities, polished mentorship, or well-structured onboarding. It began in a village studio with an old laptop, slow electricity, and a kid who didn’t even know what “design” meant — only that creating something felt like magic.<br /><br />
            A corporate résumé can list skills, but it can’t capture what shaped them. My skills didn’t come from shiny offices or safe career ladders. They came from trying, failing, trying again, supporting family, rebuilding after setbacks, and choosing to learn when it would have been easier to quit.<br /><br />
            This isn’t a lack of professionalism — it’s the reason I’m good at what I do. Everything I create today comes from a place of curiosity, pressure, responsibility, and stubborn growth. I write my journey like this because it’s true. Because the real story matters more than corporate vocabulary. And because I’m still learning — still improving — still building — and that part will never stop.
          </p>
        </motion.div>

        <div className="journey-layout">
          <motion.div
            className="story-column"
            initial="hidden"
            animate={isInView ? 'visible' : 'hidden'}
          >
            {acts.map((act, index) => (
              <motion.div
                key={act.id}
                className="story-act"
                variants={actVariants}
                custom={index}
              >
                <div className="act-meta">
                  <span className="act-range">{act.range}</span>
                  <h3 className="act-title">{act.title}</h3>
                </div>
                <div className="act-lines">
                  {act.lines.map((line, idx) => (
                    <p key={idx} className="act-line">
                      {line}
                    </p>
                  ))}
                </div>
              </motion.div>
            ))}
          </motion.div>

          <motion.div
            className="timeline-column"
            initial="hidden"
            animate={isInView ? 'visible' : 'hidden'}
          >
            <div
              className="timeline-track"
              style={{ '--glow-offset': `${glowPercent}%` } as React.CSSProperties}
            />

            <div className="timeline-stack">
              {timelineItems.map((item, index) => (
                <TimelineCard key={`${item.year}-${index}`} item={item} index={index} />
              ))}
            </div>

            <motion.div
              className="journey-takeaways"
              variants={cardVariants}
              initial="hidden"
              animate={isInView ? 'visible' : 'hidden'}
              transition={{ delay: 0.25 }}
            >
              <h4>{t('journey.takeaways.title')}</h4>
              <ul className="takeaway-list">
                {takeaways.map((item, idx) => (
                  <li key={idx}>{item}</li>
                ))}
              </ul>
              <motion.a
                href="/journey"
                className="btn btn-primary journey-cta-button"
                whileHover={{ y: -2, scale: 1.01 }}
                whileTap={{ scale: 0.99 }}
              >
                Read the full story →
              </motion.a>
            </motion.div>
          </motion.div>
        </div>
      </div>
    </section>
  );
};

export default Journey;
